// ProductDetail.jsx
import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import Modal from 'react-modal';
import ChatComponent from './ChatComponent'; // import ChatComponent

const ProductDetail = () => {
  const { productId } = useParams();
  const [product, setProduct] = useState(null);
  const [showChatModal, setShowChatModal] = useState(false);
  const [userType, setUserType] = useState('buyer'); // or seller or undefined

  useEffect(() => {
    Modal.setAppElement('#root'); // or any other root element ID where the modal is rendered
  }, []);

  useEffect(() => {
    const fetchProductDetail = async () => {
      try {
        const response = await fetch(`https://ec2caps.liroocapstone.shop:4000/products/detail/${productId}`);
        if (response.ok) {
          const data = await response.json();
          setProduct(data);
          // 여기서 로그인된 사용자의 정보를 가져와 userType을 설정할 수 있음
          // setUserType(...);
        } else {
          console.error('Error fetching product detail:', response.status);
        }
      } catch (error) {
        console.error('Error fetching product detail:', error);
      }
    };

    fetchProductDetail();
  }, [productId]);

  if (!product) {
    return <div>Loading...</div>;
  }

  const availability = product.status === 'available' ? '구매 가능' : '판매 완료';

  return (
    <div className="product-detail">
      <h2 className="product-name">{product.name}</h2>
      <p className="product-description">{product.description}</p>
      <div className="product-info">
        <p className="product-price">Price: ${product.price}</p>
        <p className="product-date">Date: {new Date(product.createdAt).toLocaleDateString()}</p>
        <p className="product-views">Views: {product.views}</p>
        <p className="product-availability">Availability: {availability}</p>
        {userType === 'buyer' && (
          <button onClick={() => setShowChatModal(true)}>채팅하기</button>
        )}
      </div>
      <ChatComponent
        userType={userType}
        showChatModal={showChatModal}
        setShowChatModal={setShowChatModal}
      />
    </div>
  );
};

export default ProductDetail;
