import React, { useState, useEffect, useRef } from 'react';
import Modal from 'react-modal';
import Draggable from 'react-draggable';

const ChatComponent = ({ showChatModal, setShowChatModal, recipient, productId }) => {
  const userId = sessionStorage.getItem('userId');
  const [chatMessage, setChatMessage] = useState('');
  const [chatMessages, setChatMessages] = useState([]);
  const ws = useRef(null);
  const ref = useRef(null);
  const dragRef = useRef(null);

  useEffect(() => {
    // WebSocket 연결 설정
    ws.current = new WebSocket(`ws://ec2caps.liroocapstone.shop:4001/${userId}/${productId}`);
  
    // WebSocket 이벤트 리스너
    ws.current.onopen = () => {
      console.log('WebSocket 연결 성공');
    };

    ws.current.onmessage = (event) => {
      const receivedMessage = JSON.parse(event.data);
      console.log('Received message:', receivedMessage);
        
      // 받은 메시지를 채팅 메시지에 추가
      setChatMessages(prevMessages => [...prevMessages, receivedMessage]);
    };

    ws.current.onerror = (error) => {
      console.error('WebSocket 연결 오류:', error);
    };

    ws.current.onclose = () => {
      console.log('WebSocket 연결 종료');
    };

    // 컴포넌트 언마운트 시 WebSocket 연결 종료
    return () => {
      if (ws.current) {
        ws.current.close();
      }
    };
  }, [userId, productId]); // userId나 productId 변경 시에만 연결 재설정
  
  const handleSendMessage = () => {
    const message = chatMessage.trim();
    if (message !== '') {
      // 클라이언트에서 서버로 메시지 전송
      ws.current.send(JSON.stringify({ content: message, sender: userId, receiver: recipient }));
      setChatMessages(prevMessages => [...prevMessages, { content: message, sender: userId, receiver: recipient }]);
      setChatMessage('');
    }
  };
  
  return (
    <Modal
      isOpen={showChatModal}
      onRequestClose={() => setShowChatModal(false)}
      overlayClassName="modal-overlay"
    >
      <Draggable handle=".modal-header" nodeRef={dragRef}>
        <div className="modal-container resizable-modal" ref={dragRef}>
          <div className="modal-header">
            1:1 채팅 - {recipient}님과 (상품 ID: {productId})
          </div>
          <div className="modal-body">
            <div className="chat-messages">
              {chatMessages.map((message, index) => (
                <div key={index} className={`chat-message ${message.sender === userId ? 'own-message' : 'other-message'}`}>
                  <span className="message-sender">{message.sender}:</span> {message.content}
                </div>
              ))}
            </div>
            {userId && (
              <input
                type="text"
                className="chat-input"
                placeholder="메시지를 입력하세요"
                value={chatMessage}
                onChange={(e) => setChatMessage(e.target.value)}
                onKeyDown={(e) => {
                  if (e.keyCode === 13) handleSendMessage();
                }}
                ref={ref}
              />
            )}
            {userId && (
              <button onClick={handleSendMessage}>전송</button>
            )}
          </div>
        </div>
      </Draggable>
    </Modal>
  );
};

export default ChatComponent;
